<template>
  <div>
    <div class="cart">
      <van-nav-bar title="购物袋" :right-text="rightText" @click-right="toggleType" />

      <div class="shopbag-bg"></div>

      <div class="pro-list" v-if="shopbagData.length>0">
        <van-swipe-cell v-for="(item, index) in shopbagData" :key="index" :disabled="isEdit">
          <template>
            <div class="pro-contain">
              <van-checkbox v-model="item.isCheck" checked-color="#0b34ba" @click="sum">
                <div class="contain">
                  <img :src="item.small_img" alt />
                  <div class="pro-rules">
                    <div class="item-name">{{item.name}}</div>
                    <div class="item-rule">{{item.rule}}</div>
                  </div>
                </div>
              </van-checkbox>

              <div class="pro-sum">
                <div class="item-sum">￥{{item.price}}</div>
                <div class="item-num">
                  <van-stepper
                    v-model="item.count"
                    theme="round"
                    disable-input
                    button-size="24"
                    @change="modifyCount(item)"
                  />
                </div>
              </div>
            </div>
          </template>

          <template #right>
            <van-button square type="danger" text="删除" @click="deleteOne(item.sid, index)" />
          </template>
        </van-swipe-cell>
      </div>

      <div v-else>
        <van-empty description="咦~什么都没有呢"></van-empty>
      </div>

      <div class="clearing" v-if="shopbagData.length>0">
        <div class="text">
          <div class="total">应付合计</div>
          <div class="label">订单满￥55免配送费</div>
        </div>

        <div class="sum">￥{{total}}</div>

        <van-button color="#0b34ba" v-show="isEdit" @click="commit">去结算</van-button>
        <van-button color="#0b34ba" v-show="!isEdit" @click="deleteChecked">删除</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/less/cart.less";
import { utils } from "../../assets/js/utils";
export default {
  data() {
    return {
      shopbagData: [],

      isEdit: true,
      rightText: "编辑",
      total: "0.00",
    };
  },

  methods: {
    // 切换编辑状态
    toggleType() {
      this.isEdit = !this.isEdit;
      this.rightText = this.isEdit ? "编辑" : "完成";
    },

    // 查询购物袋数据
    findShopbag() {
      let tokenString = localStorage.getItem("owner");

      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 5000) {
            result.data.result.map((v) => {
              v.isCheck = false;
            });

            this.shopbagData = result.data.result;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    modifyCount(item) {
      let tokenString = localStorage.getItem("owner");

      if (!tokenString) return;

      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sid: item.sid,
        count: item.count,
      });

      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/modifyShopcartCount",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          this.sum();
          if (result.data.code !== 6000) {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 删除购物袋数据
    deleteGoods(sids) {
      let tokenString = localStorage.getItem("owner");

      if (!tokenString) return;

      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sids: JSON.stringify(sids),
      });

      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      return this.axios({
        method: "POST",
        url: "/deleteShopcart",
        data,
      });
    },

    // 单个删除商品
    deleteOne(sid, index) {
      this.deleteGoods([sid])
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 7000) {
            this.shopbagData.splice(index, 1);
          }

          this.$toast(result.data.msg);

          this.sum();
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 删除多个商品
    deleteChecked() {
      let sids = [];
      this.shopbagData.map((v) => {
        if (v.isCheck) {
          sids.push(v.sid);
        }
      });

      this.deleteGoods(sids)
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 7000) {
            for (let i = this.shopbagData.length - 1; i >= 0; i--) {
              if (this.shopbagData[i].isCheck) {
                this.shopbagData.splice(i, 1);
              }
            }

            this.sum();
          }

          this.$toast(result.data.msg);
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 计算总金额
    sum() {
      this.total = 0;
      this.shopbagData.map((v) => {
        if (v.isCheck) {
          this.total += v.count * v.price;
        }
      });
      this.total = this.total.toFixed(2);
    },

    // 提交订单
    commit() {
      let sids = [];
      this.shopbagData.map((v) => {
        if (v.isCheck) {
          sids.push(v.sid);
        }
      });

      if (sids == "") {
        return this.$toast("没有要埋单的商品");
      }

      sids = sids.join("-");
      this.$router.push({ name: "Pay", query: { sids } });
    },
  },

  created() {
    this.findShopbag();
  },
};
</script>